class Header extends Component {
  static propTypes = {
    member: PropTypes.shape({
      firstName: PropTypes.string,
      email: PropTypes.string,
    }),
    logout: PropTypes.func.isRequired,
    history: PropTypes.shape({
      push: PropTypes.func.isRequired,
    }).isRequired,
  }

  static defaultProps = {
    member: {},
  }

  constructor(props) {
    super(props);

    this.toggleDropDown = this.toggleDropDown.bind(this);
    this.closeDropDown = this.closeDropDown.bind(this);
    this.onLogout = this.onLogout.bind(this);
    this.state = { isOpen: false };
  }

  // onLogout = () => this.props.logout().then(() => this.props.history.push('/login'));
  onLogout() {
    this.props.logout().then(() => this.props.history.push('/login'));
  }

  toggleDropDown = () => this.setState({ isOpen: !this.state.isOpen });

  closeDropDown = () => {
    this.setState({ isOpen: false });
  }

  render() {
    const { member } = this.props;
    const loggedIn = !!(member && member.email);

    return (
      <Head className="no-print">
        <ColF>
          <NavbarI dark expand="sm" >
          </NavbarI>
          <HeaderResponsive md="12" sm="12" loggedIn={loggedIn} member={member} onLogout = {this.onLogout}/>
        </ColF>
        <CuongLinh >
          Lấy giáo dục hoằng dương Phật pháp. Lấy giảng giải bồi dưỡng nhân tài.
          Lấy từ bi lợi ích xã hội. Lấy chân thành phát triển giao lưu.
          Lấy chuyên tu cầu sanh Tịnh Độ.
        </CuongLinh>
      </Head>
    );
  }
}

export default withRouter(Header);
